.firstBlock {
    background-color: #0093E9;
    padding: 15;
    box-shadow: 0px -1px 13px 3px rgba(0, 0, 0, 0.2);
}
.firstBlock .container {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
}
.firstBlock .container .box {
    margin: 15px 0 !important;
}

.firstBlock .container .box .icon {
    color: white !important;
}
.main {
    margin: -8px;
    height: 100%;
    padding: 30px 15px;
}
.secondBlock {
    min-width: -webkit-max-content;
    min-width: max-content;
    background-color: white;
    box-shadow: 0px -1px 3px rgba(0, 0, 0, 0.2);
}
.peoples {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 5px 15px;
    transition: 0.4s;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none; 
    border-top: 1px solid #808080;
    border-bottom: 1px solid #808080;
}
.peoples:hover {
    transition: 0.4s;
    background-color: rgba(0, 147, 227, 0.4);
}

.connection {
    padding: 0 30px;
}
.connection svg {
    width: 3vw;
    height: 3vw;
}
html {
    height: 100%;
    background-color: #0093E9;
    background-image: linear-gradient(160deg, #0093E9 0%, #80D0C7 100%);
}
body,
#root {
    height: 100%;
}
